.form {
  border: 1px solid #000;
  padding: 20px;
  width: 400px;
  border-radius: 10px;
  margin-bottom: 15px;
  background-color: var(--background);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  margin: 0 auto 10px;
  position: relative;
}

.form:hover {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
  background-color: var(--background-hover);
}

.submitButton {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px auto 0 auto;
}

.backspaceIconName {
  color: grey;
  cursor: pointer;
  position: absolute;
  transition: color 250ms ease-in-out;
  right: 20px;
  top: 41px;

  &:hover {
    color: black;
  }
}

.backspaceIconPhone {
  color: grey;
  cursor: pointer;
  position: absolute;
  transition: color 250ms ease-in-out;
  right: 20px;
  top: 105px;

  &:hover {
    color: black;
  }
}
